import classNames from 'classnames/bind';

type Modifiers = Record<never, never>;

interface Styles {
  [key: string]: string;
}

type DecoratorFunction = (baseClass: string, modifiers: Modifiers, externalClasses?: Modifiers) => string;

const cssDecorator = (styles: Styles): DecoratorFunction => {
  const cn = classNames.bind(styles);

  return (baseClass, modifiers, externalClasses): string => {
    const additionalClasses = Object
      .entries(modifiers)
      .map(([key, value]) => (value ? styles[`${ baseClass }--${ key }`] : null));

    const baseClasses = cn(styles[baseClass] || '', additionalClasses);

    return externalClasses ? classNames(baseClasses, externalClasses) : baseClasses;
  };
};

export default cssDecorator;
